#app-search-autocomplete {
	width: 100%;
	height: 40px;
}

.ikt-autocomplete {
	width: 100%;
	height: auto;
	max-width: 100%;
	overflow: unset;
	box-sizing: border-box;
	opacity: 1;
	transition: opacity 0.3s linear;
	.ikt-autocomplete-input {
		width: 100%;
		box-sizing: border-box;
	}
	.ikt-autocomplete-select {
		display: block;
		width: 100%;
		max-height: 400px;
		overflow: auto;
		z-index: 2;
		box-sizing: border-box;
		.ikt-autocomplete-select-options {
			width: 100%;
			height: auto;
			border: 1px solid gainsboro;
			border-radius: 5px;
			.ikt-autocomplete-select-option {
				width: 100%;
				height: 40px;
				transition: background-color 0.2s ease-out;
				cursor: pointer;
				display: flex;
				align-items: center;
				padding: 5px
			}
			.ikt-autocomplete-select-option:hover {
				background-color: gainsboro;
				color: white;
			}
			.ikt-autocomplete-select-option.is__selected {
				background-color: #41b883;
				color: white;
			}
			.ikt-autocomplete-select-option {

				.ikt-autocomplete-select-option-tile {
					height: 40px;
					font-weight: 600;
					display: flex;
					align-items: center;
					flex-direction: column;
					justify-content: center;
					margin: 0 3px;
				}
			}
		}
	}
	.ikt-autocomplete-select.is__hidden {
		display: none;
	}
}

.tag {
	margin: 10px;
}